*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-font-family: "Montserrat", sans-serif;
  --secondary-font-family: "Seymour One", sans-serif;
  --accent-color: #f8d24e;
  --primary-bcg-color: #84a178;
  --backdrop-bcg-color: rgba(0, 0, 0, .2);
  --second-accent-color: #84a178;
  --secondary-bcg-color: #fff;
  --primary-text-color: #fcfcfc;
  --secondary-text-color: #131313;
  --address-text-color: #d6936d;
  --time-working-text-color: #907e82;
  --copyright-color: #7a7a7a;
  --icon-mobile-close-color: #fcfcfc;
  --icon-modal-close: #131313;
  --input-bcg-color: #fcf8eb;
  --product-bcg-color: #f5f5f5;
  --product-cadr-shadow-color: rgba(0, 0, 0, .3);
  --product-card-sc-shadow-color: rgba(0, 0, 0, .25);
  --img-ice-cream-bcg-color: #aec6a5;
  --img-ice-cream-ln-grad-color: rgba(156, 190, 143, .7);
  --img-ice-cream-shadow-color: rgba(10, 136, 23, .25);
  --img-ice-coffee-bcg-color: #edd276;
  --img-ice-coffee-ln-grad-color: rgba(248, 210, 78, .7);
  --img-ice-coffee-shadow-color: rgba(153, 114, 23, .25);
  --img-milkshake-bcg-color: #d6936d;
  --img-milkshake-ln-grad-color: rgba(213, 117, 63, .7);
  --img-milkshake-shadow-color: rgba(0, 0, 0, .25);
  --about-ln-gard-color-fr: rgba(248, 210, 78, .7);
  --about-ln-gard-color-sc: #d9d9d9;
  --about-shadow-color: rgba(153, 114, 23, .25);
  --review-img-bcg-color: #6e6e6e;
  --social-hv-fc-color: #fff;
  --transition-duration-time-func: 250ms cubic-bezier(.4, 0, .2, 1);
  --bcg-img-ln-grad: #fcfcfc;
  --input-true-bcg: rgba(72, 146, 67, .36);
  --input-false-bcg: rgba(202, 87, 106, .36);
  --btn-hv-fc-color: #0a8817;
  --ingrd-box-numb-shadow-color: rgba(0, 0, 0, .25);
  --contact-box-sahdow-color: rgba(212, 20, 67, .1);
  --contact-schedule-bcg-color: #e1e1e1;
  --contact-schedule-time-color: #ababab;
  --footer-bfr-border-color: #e1e1e1;
  --gallery-review-bcg-label-color: #d9d9d9;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, p {
  margin-top: 0;
  margin-bottom: 0;
}

ol, ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

a {
  cursor: url("pointer.7238bc11.png"), auto;
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: url("pointer.7238bc11.png"), auto;
  font-family: inherit;
}

body {
  font-family: var(--primary-font-family);
  color: var(--primary-text-color);
  letter-spacing: .04em;
  background-color: var(--primary-bcg-color);
  font-size: 14px;
}

html {
  scroll-behavior: smooth;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: var(--backdrop-bcg-color);
  opacity: 1;
  z-index: 10;
  transition: opacity var(--transition-duration-time-func), visibility var(--transition-duration-time-func);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-buy__scroll {
  overflow-y: scroll;
}

.modal-buy {
  width: 280px;
  background-color: var(--secondary-bcg-color);
  border: 2px solid var(--accent-color);
  z-index: 100;
  border-radius: 10px;
  padding: 8px 8px 28px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal-buy-mob-close-btn {
  width: 16px;
  height: 16px;
  transition: border-color var(--transition-duration-time-func), fill var(--transition-duration-time-func);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-buy-close-mob-icon {
  fill: var(--icon-modal-close);
  width: 16px;
  height: 16px;
}

.modal-buy-close-btn:hover .modal-buy-close-mob-icon, .modal-buy-close-btn:focus .modal-buy-close-mob-icon {
  fill: var(--accent-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-buy-close-btn, .modal-buy-close-icon {
  display: none;
}

@media screen and (min-width: 768px) {
  .modal-buy-mob-close-btn, .modal-buy-close-mob-icon {
    display: none;
  }

  .modal-buy-close-btn {
    width: 20px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    justify-content: center;
    align-items: center;
    padding: 0;
    transition: border-color .25s cubic-bezier(.4, 0, .2, 1), fill .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .modal-buy-close-icon {
    width: 20px;
    height: 20px;
    fill: var(--icon-modal-close);
    transition: fill .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .modal-buy-close-btn:hover .modal-buy-close-icon, .modal-buy-close-btn:focus .modal-buy-close-icon {
    fill: var(--accent-color);
  }
}

.modal-buy-form {
  flex-direction: column;
  display: flex;
}

