  align-items: center;
  display: flex;
  position: absolute;
  top: -1px;
  left: -1px;
}

.buy-now__box-title:before {
  content: "";
  width: 132px;
  height: 140px;
  background-repeat: no-repeat;
  border-radius: 50%;
