  box-shadow: 0px 5.40397px 20.2649px var(--img-ice-coffee-shadow-color);
  position: absolute;
  top: -50%;
  left: -1px;
}

.buy-now__box-third {
  border: 2px solid var(--img-milkshake-bcg-color);
}

.buy-now__box-third:before {
  background-image: url("buy3.ad83be07.png"), linear-gradient(0deg, var(--img-milkshake-ln-grad-color), var(--img-milkshake-ln-grad-color) );
  box-shadow: 0px 5.41333px 20.3px var(--img-milkshake-shadow-color);
  position: absolute;
  top: -50%;
  left: -1px;
}

.buy-now-card-title {
  z-index: 3;
  text-align: center;
  text-transform: uppercase;
  color: var(--secondary-text-color);
