  color: var(--secondary-text-color);
  background-color: var(--input-bcg-color);
  border: 1px solid var(--primary-bcg-color);
  resize: none;
  transition: border-color var(--transition-duration-time-func);
  border-radius: 10px;
  margin-bottom: 25px;
