  border-radius: 10px;
  align-self: center;
  padding: 10px 52px;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.25;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal-buy-form-button:hover, .modal-buy-form-button:focus {
  background-color: var(--btn-hv-fc-color);
}

.modal-buy-form-button:focus {
  color: var(--primary-text-color);
}

@media screen and (min-width: 768px) {
  .modal-buy {
    min-width: 568px;
